    onShow(){
      let userId = uni.getStorageSync('ID')
      this.$http.carOwnerDetail({'createUserId':userId}).then(res=>{
        console.log('res =>',res.data)
        this.params = res.data && res.data[0]
        console.log(res)
        let carDTO = res.data && res.data
        let carInfo = []
        carDTO.forEach(e => {
          let carList = {
            licensePlate: null,
            insuranceId : null,
            ownerId: null,
          }
          let carInfoDetail = e.carBrandDTO
          carList.carBrandDTO = carInfoDetail
          carList.licensePlate = e.licensePlate // 车牌号
          carList.insuranceId = (e.insuranceCompany && e.insuranceCompany.name) ? e.insuranceCompany.name : '-' //保险公司名称
          carList.ownerId = e.createUserId //创建人ID
          carInfo.push(carList)
        })
        carInfo[0].active = 0
        this.carItems = carInfo
      })
      this.$http.rescueInfoList({'ownerId':userId}).then(res=>{
        console.log('res =>',res)
        this.rescueItems = res.data.records
      })
      this.$http.planRouteList({'createUserId':userId}).then(res=>{
        console.log('emergencyCourseItems =>',res)
        this.emergencyCourseItems = res.data.records
      })
